.profile__info {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  justify-content: center;
  align-self: center;
  column-gap: 24px;
  row-gap: 16px;
}

@media screen and (max-width: 425px) {
  .profile__info {
  column-gap: 12px;
  row-gap: 14px;
  }
}
